import { getImagesByQuery } from "./js/pixabay-api.js";
import { createGallery, clearGallery, showLoader, hideLoader } from "./js/render-functions.js";
import iziToast from "izitoast";
import "izitoast/dist/css/iziToast.min.css";

const form = document.querySelector(".form");
const searchInput = form.querySelector("input[name='search-text']");
const gallery = document.querySelector(".gallery");
const loadMoreBtn = document.getElementById("load-more-btn");
const loader = document.getElementById("loader");

let currentQuery = "";
let currentPage = 1;
const perPage = 15;

form.addEventListener("submit", async (event) => {
  event.preventDefault();
  const query = searchInput.value.trim();

  if (!query) {
    iziToast.warning({
      title: "Warning",
      message: "Please enter a search query!",
    });
    return;
  }

  // –û—á–∏—â–∞—î–º–æ –≥–∞–ª–µ—Ä–µ—é –ø–µ—Ä–µ–¥ –∫–æ–∂–Ω–∏–º –Ω–æ–≤–∏–º –ø–æ—à—É–∫–æ–º
  clearGallery();
  hideLoadMore(); // –°—Ö–æ–≤—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load more" –ø—Ä–∏ –ø–æ—à—É–∫—É
  showLoader(); // –ü–æ–∫–∞–∑—É—î–º–æ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è

  currentQuery = query;
  currentPage = 1; // –°–∫–∏–¥–∞—î–º–æ —Å—Ç–æ—Ä—ñ–Ω–∫—É –¥–æ –ø–µ—Ä—à–æ—ó

  // –û—Ç—Ä–∏–º—É—î–º–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  const { hits, totalHits } = await getImagesByQuery(query, currentPage, perPage);
  hideLoader(); // –•–æ–≤–∞—î–º–æ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –ø—ñ—Å–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è

  if (hits.length === 0) {
    iziToast.error({
      title: "Error",
      message: "Sorry, there are no images matching your search query. Please try again!",
    });
    return;
  }

  // –î–æ–¥–∞—î–º–æ –Ω–æ–≤—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  createGallery(hits);

  if (currentPage * perPage < totalHits) {
    showLoadMore(); // –ü–æ–∫–∞–∑—É—î–º–æ –∫–Ω–æ–ø–∫—É "Load more", —è–∫—â–æ —î —â–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  } else {
    hideLoadMore();
    iziToast.info({
      title: "Info",
      message: "We're sorry, but you've reached the end of search results.",
    });
  }

  // –ü–ª–∞–≤–Ω–µ –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è –ø—ñ—Å–ª—è –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å
  scrollToNewImages();
});

loadMoreBtn.addEventListener("click", async () => {
  currentPage += 1; // –ó–±—ñ–ª—å—à—É—î–º–æ –Ω–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏
  showLoaderUnderButton(); // –ü–æ–∫–∞–∑—É—î–º–æ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –¥–ª—è –∫–Ω–æ–ø–∫–∏

  const { hits, totalHits } = await getImagesByQuery(currentQuery, currentPage, perPage);
  hideLoaderUnderButton(); // –•–æ–≤–∞—î–º–æ —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä

  // –î–æ–¥–∞—î–º–æ –Ω–æ–≤—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –¥–æ —ñ—Å–Ω—É—é—á–∏—Ö
  createGallery(hits);

  if (currentPage * perPage >= totalHits) {
    hideLoadMore(); // –•–æ–≤–∞—î–º–æ –∫–Ω–æ–ø–∫—É, —è–∫—â–æ –±—ñ–ª—å—à–µ –Ω–µ–º–∞—î –∑–æ–±—Ä–∞–∂–µ–Ω—å
    iziToast.info({
      title: "Info",
      message: "We're sorry, but you've reached the end of search results.",
    });
  }

  // –ü–ª–∞–≤–Ω–µ –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è –ø—ñ—Å–ª—è –¥–æ–¥–∞–≤–∞–Ω–Ω—è –Ω–æ–≤–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
  scrollToNewImages();
});

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–ª–∞–≤–Ω–æ–≥–æ –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏
function scrollToNewImages() {
  const firstImageCard = gallery.querySelector("li"); // –ø–µ—Ä—à–∏–π –µ–ª–µ–º–µ–Ω—Ç –≤ –≥–∞–ª–µ—Ä–µ—ó
  if (firstImageCard) {
    const cardHeight = firstImageCard.getBoundingClientRect().height;
    window.scrollBy({
      top: cardHeight * 2, // –ø—Ä–æ–∫—Ä—É—á—É–≤–∞–Ω–Ω—è –Ω–∞ –¥–≤—ñ –≤–∏—Å–æ—Ç–∏ –∫–∞—Ä—Ç–∫–∏
      behavior: "smooth", // –ø–ª–∞–≤–Ω–∞ –∞–Ω—ñ–º–∞—Ü—ñ—è
    });
  }
}

// Show/hide button and loader under it
function showLoadMore() {
  loadMoreBtn.hidden = false;
}

function hideLoadMore() {
  loadMoreBtn.hidden = true;
}

function showLoaderUnderButton() {
  loader.classList.add("visible");
}

function hideLoaderUnderButton() {
  loader.classList.remove("visible");
}
